<template>
  <div class="dashboard">
    <Navbar />
    <h2>Dit is de admin dashboard</h2>
    <p v-for="test in tests" :key="test.id">{{ test.textinput }}</p>
  </div>
</template>

<script>
import Navbar from '@/components/layout/Navbar'
import db from '@/firebase/init'
import firebase from 'firebase'

export default {
  name: 'Dashboard',
  components: {
      Navbar
  },
  data(){
    return{
      tests: []
    }
  },
  created(){
    db.collection('tests').get()
    .then(snapshot => {
      snapshot.forEach(doc => {
        let test = doc.data()
        test.id = doc.id
        this.tests.push(test)
      })
    })
  }
}
</script>
